import { Component, Input, OnInit } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { Order } from 'src/app/interfaces/order';
import { MapService } from 'src/app/services/sys/map.service';

@Component({
  selector: 'app-note',
  templateUrl: './note.component.html',
  styleUrls: ['./note.component.css'],
})
export class NoteComponent implements OnInit {
  public note: string;

  constructor(
    private sysMap: MapService,
    public viewCtrl: PopoverController
  ) { }
  @Input() order: Order;
  ngOnInit() {
    this.note = localStorage.getItem(this.order.id.toString());
  }
  public saveNote() {
    localStorage.setItem(this.order.id.toString(), this.note);
    this.sysMap.infoUpdated.next();
    this.viewCtrl.dismiss();
  }
}
